import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import NewContact from '@/components/NewContact'
import NewContact2 from '@/components/NewContact2'
import NewContact3 from '@/components/NewContact3'
import NewContact4 from '@/components/NewContact4'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/newcontact',
      name: 'NewContact',
      component: NewContact
    },
    {
      path: '/newcontact2',
      name: 'NewContact2',
      component: NewContact2
    },
    {
      path: '/newcontact3',
      name: 'NewContact3',
      component: NewContact3
    },
    {
      path: '/newcontact4',
      name: 'NewContact4',
      component: NewContact4
    }
  ]
})



