<template>
  <el-row class="newcontact">
    <el-col :xs="24" :sm="18" :md="14" :lg="10" id="main">
      <label>批次：</label>
      <el-select v-model="info.action" placeholder="请选择">
        <el-option v-for="item in actions" :key="item" :value="item"></el-option>
        <!-- 这里的key官方推荐在v-for时使用，不然会警告，但不影响使用 -->
      </el-select>
      <label>釜次：</label>
      <el-select v-model="info.kettle" placeholder="请选择">
        <el-option v-for="item in kettles" :key="item" :value="item"></el-option>
      </el-select>
      <label>步骤：</label>
      <el-select v-model="info.step" placeholder="请选择">
        <el-option v-for="item in steps" :key="item" :value="item"></el-option>
      </el-select>
      <label>姓名：</label>
      <el-select v-model="info.name" placeholder="请选择">
        <el-option v-for="item in names" :key="item" :value="item"></el-option>
      </el-select>
      <el-button class="btn-auto" @click="create" type="success">创建</el-button>
      <template>
        <el-table :data="tabledata" align="left">
          <el-table-column prop="action" label="批次"></el-table-column>
          <el-table-column prop="kettle" label="釜次"></el-table-column>
          <el-table-column prop="step" label="步骤"></el-table-column>
          <el-table-column prop="name" label="姓名"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="a">
              <el-button size="mini" type="danger" @click="del(a.$index)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </el-col>
  </el-row>
</template>
<script>
import Storage from '../store/store'

export default {
  name: 'NewContact',
  data () {
    return {
      info: {
        name: '',
        step: '',
        action: '',
        kettle: ''
      },
      actions: [
        'CA0021-0427', 'CA0021-0314', 'CA0021-0411'
      ],
      names: [
        '张三', '李四', '王五', '赵六'
      ],
      steps: [
        '核料', '加水', '保温'
      ],
      kettles: [
        '25', '28', '14'
      ],
      tabledata: Storage.fetch()
    }
  },
  methods: {
    create () {
      this.tabledata.push(this.info)
      this.info = {action: '', kettle: '', step: '', name: ''}
    },
    del (index) {
      this.tabledata.splice(index, 1)
    }
  },
  watch: {
    tabledata: {
      handler (items) {
        Storage.save(items)
      },
      deep: true
    }
  }
}
</script>
<style>
  #main {
    float: none;
    margin: 0 auto;
  }

  .el-input {
    padding-bottom: 5px;
  }

  .el-select {
    display: block;
  }

  .btn-auto {
    width: 100%;
    margin-top: 12px;
  }
</style>



