<template>
  <el-row class="newcontact2">
    <el-col :xs="24" :sm="18" :md="14" :lg="10" id="main">
      <label>姓名：</label>
      <el-select v-model="info.name2" placeholder="请选择">
        <el-option v-for="item in names2" :key="item" :value="item"></el-option>
      </el-select>
      <label>釜次：</label>
      <el-select v-model="info.kettle2" placeholder="请选择">
        <el-option v-for="item in kettles2" :key="item" :value="item"></el-option>
      </el-select>
      <label>步骤：</label>
      <el-select v-model="info.step2" placeholder="请选择">
        <el-option v-for="item in steps2" :key="item" :value="item"></el-option>
      </el-select>
      <div class="block">
        <label>分发：{{ actions2 }}</label>
        <div></div>
        <el-cascader
          :options="options"
          :props="props"
          collapse-tags
          v-model="info.actions2"
          clearable></el-cascader>
      </div>
      <el-button class="btn-auto" @click="create" type="success">创建</el-button>
      <template>
      <el-table :data="tabledata2" align="left">
        <el-table-column prop="name2" label="姓名"></el-table-column>
        <el-table-column prop="kettle2" label="釜次"></el-table-column>
        <el-table-column prop="step2" label="步骤"></el-table-column>
        <el-table-column prop="actions2" label="动作"></el-table-column>
        <el-table-column label="操作">
        <template slot-scope="a">
            <el-button size="mini" type="danger" @click="del(a.$index)">删除</el-button>
          </template>
        </el-table-column>
        </el-table>
      </template>
    </el-col>
  </el-row>
</template>

<script>
import Storage from '../store/store2'

export default {
  data () {
    return {
      props: {multiple: true},
      options: [{
        value: '主',
        label: '主操',
        children: [{
          value: '1',
          label: '动作一'
        }, {
          value: '2',
          label: '动作二'
        }]
      }, {
        value: '辅',
        label: '辅操',
        children: [{
          value: '1',
          label: '动作一'
        }, {
          value: '2',
          label: '动作二'
        }, {
          value: '3',
          label: '动作三'
        }]
      }],
      info: {
        name2: '',
        kettle2: '',
        actions2: '',
        strp2: ''
      },
      names2: [
        '张三', '李四', '王五', '赵六'
      ],
      kettles2: [
        '25', '28', '14'
      ],
      steps2: [
        '核料', '保温'
      ],
      tabledata2: Storage.fetch()
    }
  },
  methods: {
    create () {
      this.tabledata2.push(this.info)
      this.info = {name2: '', kettle2: '', options: '', actions2: '', step2: ''}
    },
    del (index) {
      this.tabledata2.splice(index, 1)
    }
  },
  watch: {
    tabledata2: {
      handler (items) {
        Storage.save(items)
      },
      deep: true
    }
  }
}
</script>
<style>
  #main {
    float: none;
    margin: 0 auto;
  }

  .el-input {
    padding-bottom: 5px;
  }

  .el-select {
    display: block;
  }

  .btn-auto {
    width: 100%;
    margin-top: 12px;
  }

  .el-cascader {
    display: block;
  }

  .el-table {
    margin: 10px 0;
  }

</style>



