<template>
  <div id="app">
    <el-row :gutter="24" style="height: 60px;">
      <el-col :span="10"><div class="grid-content bg-purple"><img src="./assets/logo01.png" style="margin: 12px 0"></div></el-col>
      <el-col :span="10"><div class="grid-content bg-purple" style="padding: 0 0"><h5 style="text-align: left">安全质量技术管理系统</h5></div></el-col>
      <el-col :span="4"><div class="grid-content bg-purple"><el-button type="text" @click="open"><el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar></el-button></div></el-col>
    </el-row>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  methods: {
    open () {
      this.$prompt('请输入账号', '登录', {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      }).then(({ value }) => {
        this.$message({
          type: 'success',
          message: '成功: ' + value
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消'
        })
      })
    }
  }
}
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 5px;
  }
</style>



