<template>
  <ul>
    <li v-for = '(list) in headerList' :key = 'list.id'>
      <router-link :to = '{name:list.name}'>
        <h2>{{list.title}}</h2>
      </router-link>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'Hello Word',
  data () {
    return {
      headerList: [
        {id: '1', name: 'NewContact', title: '步骤分发管理'},
        {id: '2', name: 'NewContact2', title: '动作分发管理'},
        {id: '3', name: 'NewContact3', title: '分发进度管理'},
        {id: '4', name: 'NewContact4', title: '开始工作'}
      ]
    }
  }
}
</script>

<style>
  h2 {
    color: #42b983;
  }
  ul,li {
    list-style:none;
  }
  a {
    text-decoration-line: none;
  }
</style>



