<template>
  <div>
    <el-button type="text" @click="open" style="padding: 0 0"><span>报告异常</span></el-button>
    <el-row>
      <el-col :span="6">
        <div style="height: 500px;">
          <el-steps direction="vertical" :active="1">
            <el-step title="步骤 1"></el-step>
            <el-step title="步骤 2"></el-step>
            <el-step title="步骤 3" description=""></el-step>
            <el-step title="步骤 4" description=""></el-step>
            <el-step title="步骤 5" description=""></el-step>
            <el-step title="步骤 6" description=""></el-step>
            <el-step title="步骤 7" description=""></el-step>
          </el-steps>
        </div>
      </el-col>
      <el-col :span="18"></el-col>
    </el-row>
    <el-button type="primary" size="large" v-show="show" @click="getCode"><span>开始</span></el-button>
    <el-button v-show="!show" class="count" type="primary" plain size="large" disabled><span>剩余 {{count}} s</span>
    </el-button>
    <!--    <span v-show="!show" class="count">{{count}} s</span>-->
  </div>
</template>

<script>
export default {
  name: 'Register',
  data () {
    return {
      show: true,
      count: '',
      timer: null
    }
  },
  methods: {
    getCode () {
      const TIME_COUNT = 10
      if (!this.timer) {
        this.count = TIME_COUNT
        this.show = false
        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= TIME_COUNT) {
            this.count--
          } else {
            this.show = true
            clearInterval(this.timer)
            this.timer = null
          }
        }, 1000)
      }
    }
  }
}

</script>
<style>
  #main {
    float: none;
    margin: 0 auto;
  }

  .el-input {
    padding-bottom: 5px;
  }

  .el-select {
    display: block;
  }

  .btn-auto {
    width: 100%;
    margin-top: 12px;
  }

  .el-cascader {
    display: block;
  }

  .el-table {
    margin: 10px 0;
  }
</style>



